<template>
  <div class="con">
    <publicTop></publicTop>
    <div class="home-top">
      <div class="home-top-l">
        <img src="@/assets/left-le.png" @click="goB" />
      </div>
    </div>
    <div class="video-div">
      <video :src="video" controls="controls"></video>
    </div>
  </div>
</template>     
<script>
import publicTop from "@/components/publicTop.vue";
export default {
  name: "xuanVideo",
  components: {
    publicTop,
  },
  data() {
    return {
      video: "",
    };
  },
  methods: {
    goB() {
      this.$router.go(-1);
    },
  },
  created() {
    console.log(this.$store.getters.getVideourl, "getVideourl");
    this.video = this.$store.getters.getVideourl;
  },
};
</script>
<style lang="css" scoped>
.con {
  background-color: #f5f5f5;
  padding-bottom: 0.66rem;
  min-height: 100%;
}
.home-top {
  width: 7.5rem;
  padding: 0rem 0.4rem;
  box-sizing: border-box;
  height: 0.88rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.home-top-l {
  display: flex;
  align-items: center;
  justify-content: center;
}
.home-top-l img {
  width: 0.2rem;
  height: 0.3rem;
}
.video-div {
  width: 7.5rem;
  height: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
video {
  width: 7.5rem;
  height: 4rem;
}
</style>



